</undefined></template>

<script>
export default {
  data() {
    return {
      dataSource1: [],
      targetKeys1: [],
      dataSource2: [],
      targetKeys2: [],
      dataSource3: [],
      targetKeys3: [],
      dataSource4: [],
      targetKeys4: [],
    }
  },
  created() {
    this.mockData();
    this.getMock()
  },
  methods: {
    mockData() {
      for (let i = 0; i < 20; i++) {
        this.dataSource1.push({
          key: i.toString(),
          title: `content${i + 1}`,
          description: `description of content${i + 1}`,
          disabled: i % 3 < 1,
        });
        if(i % 3 > 1) {
          this.targetKeys1.push(i.toString());
        }
      }
    },
    mockData2(index) {
      const targetKeys = [];
      const mockData = [];
      for (let i = 0; i < 20; i++) {
        const data = {
          key: i,
          title: `${i + 1}`,
          description: `${i + 1}`,
          chosen: Math.random() * 2 > 1,
        };
        if (data.chosen) {
          targetKeys.push(data.key);
        }
        mockData.push(data);
      }
      this[`dataSource${index}`] = mockData;
      this[`targetKeys${index}`] = targetKeys;
    },
    getMock() {
      for(let num = 1; num < 4; num ++) {
        this.mockData2(num+1);
      }
    },
    render1(item) {
      return item.title;
    },
    render2(item) {
      return {
        label: `${item.title} - ${item.description}`,
        value: item.title
      }
    },
    handleChange1(targetKeys, direction, moveKeys) {
      this.targetKeys1 = targetKeys;
    },
    handleChange2(targetKeys, direction, moveKeys) {
      this.targetKeys2 = targetKeys;
    },
    handleChange3(targetKeys, direction, moveKeys) {
      this.targetKeys3 = targetKeys;
    },
    handleChange4(targetKeys, direction, moveKeys) {
      this.targetKeys4 = targetKeys;
    },
    filterOption(inputValue, option) {
        return option.description.indexOf(inputValue) > -1;
    }
  }
}
</script>


